<template>
  <div class="emoji-picker" v-if="show">
    <v-card height="300px" v-if="show">
      <v-card-title class="blue white--text">
        <span class="headline">Emoji Picker</span>
      </v-card-title>
      <v-card-text>
        <div class="emoji-content">
          <emoji v-for="emoji in emojis" v-bind:key="emoji.key" :emoji="emoji" @click="onEmojiClick"></emoji>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
  
  import Emoji from './Emoji.vue'
  import axios from 'axios'
  export default {
    data () {
      return {
        emojis: []
	 
      }
    },
    props: {
      show: true,
      message: '',
      input: ''
    },
    created () {
      axios.get('https://raw.githubusercontent.com/shanraisshan/EmojiCodeSheet/master/json/string/People.json').then(response => {
        this.emojis = response.data.peoples.people
      }, response => {
        // error callback
      })
    },
    methods: {
      onEmojiClick (emoji) {

	// initialization of the input reference
	this.$emit('click')
	if(this.input){
	this.input.focus()
	console.log(this.input);
	const cursorPosition = this.input.selectionEnd
        const start = this.message.substring(0, this.input.selectionStart)
        const end = this.message.substring(this.input.selectionStart)
        const text = start + emoji.value + end
	this.$emit('add-emoji', '')
	this.$emit('add-emoji', text)
	this.input.focus()
	//this.$nextTick(() => {
        	//this.input.selectionEnd = cursorPosition + emoji.length
     			// })
	 }

      },
      closePicker () {
        this.$emit('close')
      }
    },
    components: {
      'emoji': Emoji
    }
  }
</script>

