<template>
	<div v-if="isCurrentUser(chatmessage.user)">
	 <p class="messageText colorDark" color="primary lighten-4">{{`${chatmessage.createdAt.getDate()}/${chatmessage.createdAt.getMonth()+1}/${chatmessage.createdAt.getFullYear()}`}}</p>	
		<div class="messageContainer justifyEnd">
		  	   <p class="sentText pr-10">{{chatmessage.user}}</p>
			<div class="messageBox backgroundBlue">
	 	  	   <p class="messageText colorWhite" v-html="check(chatmessage.text)" color="primary lighten-4"></p>
			</div>
		</div>
	</div>
	<div v-else>
		<div class="messageContainer justifyEnd" >
			<div class="messageBox backgroundLight">
	 	  	  <p class="messageText colorDark" v-html="chatmessage.text"></p> 
			</div>
			  <p class="sentText pr-10">{{chatmessage.user}}</p>
		</div>
	</div>		 
</template>

<script>
  import Image from './Image.vue'
  export default {
    data () {
      return {}
    },
    props: [
      'username',
      'chatmessage'
    ],
    methods: {
	 
	check(value){
		 
	        var urlPattern = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/ig;
		value=value.replace(urlPattern, "<a href='$1' target='_blank' style='color:black'>$1</a>");
		 
		return value;
	},

	isCurrentUser(user){
		var isCurrent=false;
		if(user===this.$route.params.username)
			isCurrent=true;
			
		return isCurrent 
		}
    } 
  }
</script>

