<template>
	<div>
	<div>
		<div v-for="todo in allTodos" :key="todo.id">
			{{todo.title}}
		</div>
	</div>
	<div v-if="isCurrentUser(message.user)">	
		  <div class="username" v-if="index>0 && messages[index-1].user != message.user">{{message.user}}</div>
      		  <div class="username justifyEnd" v-if="index == 0">{{message.user}}</div>
      		  <div style="margin-top: 5px"></div>
		  <div class="messageBox backgroundBlue">
     		  	<div class="messageText colorWhite">
           			 <div v-html="check(message.text)"></div>
			 		<div v-if="isPicture(message.text)">
				  		 <img :src="image" height="150" width="115">
					</div>
			</div>
		  </div>
	</div>
	<div v-else>
		  <div class="username" v-if="index>0 && messages[index-1].user != message.user">{{message.user}}</div>
      		  <div class="username" v-if="index == 0">{{message.user}}</div>
      		  <div style="margin-top: 5px"></div>
     		  <div class="content">
     		  	<div v-html="check(message.text)"></div>
				<div v-if="isPicture(message.text)">
					 <img :src="image" height="150" width="115">
				</div>
      		 </div>	 
	</div>
	</div>
</template>

<script>
  import Image from './Image.vue'
  import fileService from '../services/fileService.js'
  import { mapGetters, mapActions } from 'vuex';

  var fs = require('browserify-fs');
  export default {
    data () {
      return {val:'', mess: '', currentRef: {}, image: ''}
    },
   
    computed: mapGetters(['allTodos']),
	
    props: [
      'message',
      'index',
      'username',
      'messages',
      'uploadPercentage',
      'getFile'
    ],
    components: {
      'chat-image': Image
    },
    
    methods: {
	
	  ...mapActions(['getFiles']),

	isPicture(value,uploadPer){
		var isPic = false;
		
		var imageRegex = /([^\s\']+).(?:jpg|jpeg|gif|png)/i;
		if(value.length!=value.replace(imageRegex,"'$1'").length){
				isPic = true;
				this.getPic(value);
				}
		return isPic
		},
         check(value){
	        var urlPattern = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/ig;
		value=value.replace(urlPattern, "<a href='$1' target='_blank' style='color:black'>$1</a>");
		
		return value
		},
	async getPic(file){
		    
			this.image = 'data:image/jpeg;base64,' + await fileService.getPicture(file);
		 },
	     
	 isCurrentUser(user){
		var isCurrent=false;
		if(user===this.$route.params.username)
			isCurrent=true;
			
		return isCurrent 
		}
    	},
	created(){

		this.getFiles();
		},
	  watch:{
		'$route.params.username' (newId, oldId){
			 //console.log("path has changed");
			 //this.$router.push('https://www.fer.hr');
				 
			}
		}
	 
	
  }
</script>

