<template>
<v-app>
  <v-container fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md8>
        <v-card class="elevation-12">
          <v-toolbar dark color="blue">
            <v-toolbar-title>Signup form</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
	        <v-alert
		    :value="true"
		    color="error"
		    prepend-icon="warning"
		>
		   This user already exist!
		</v-alert>
             <v-text-field
                 
                name="userName"
		label="Username"
		:rules="[rules.required]"
		v-model = "username"
                
              ></v-text-field>

              <v-text-field
                 
                name = "email"
		label = "Email"
		:rules="[rules.required, rules.email]"
		v-model = "email"
               
              ></v-text-field>

              <v-text-field
                
                name="password"
                label="Password"
		:rules="[rules.required]"
		 v-model = "password"
               
              ></v-text-field>

              <v-text-field
                 
                name="password"
                label="Confirm Password"
		:rules="[rules.required]"
		:error = !valid()
		v-model = "confirm_password"
              
              ></v-text-field>
            </v-form>
          </v-card-text>
      	   <v-divider light></v-divider>
	      <v-card-actions>
            	<v-btn to="/" rounded color="black" dark>Login</v-btn>
            	<v-spacer></v-spacer>
            	<v-btn rounded color="success" @click.prevent="register()">Register<v-icon></v-icon></v-btn>
              </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</v-app>
</template>

<script>

import registerService from '../services/registerService';

export default {

	name: 'signUp',
	data: () => ({
		username: '',
		email: '',
		password: '',
		confirm_password: '',
		user: [], 
		rules: {
		   required: value => !!value || "Required",
		   email: value => {
			const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;			
			return pattern.test(value) || 'Invalid email.';
			}
		}
	}),
	methods:{
		async register(){
		     this.user=[this.username, this.email,this.password];
		     console.log(typeof(this.user));
		     registerService.registerUser(this.user);
		     this.$router.push('/');	
		},
		valid(){
			return this.password==this.confirm_password;

		}
	}

};

</script>
