<template>
  <v-app>
    <v-container fill-height>
	<v-layout align-center justify-center>
		<v-flex xs12 sm8 md4>
			<v-form>
			   <v-card class="elevation-12">
				<v-toolbar dark color="blue">
					<v-toolbar-title>
						Login form 
					</v-toolbar-title>
				</v-toolbar>
				<v-alert
				  color="error"
				  :value="error"
				>
				The username or password are incorrect!
				</v-alert>
			   	<v-card-text>
				    <v-text-field v-model = "username"
						  name = "login"
						  label = "Login"
						  type = "text"
				     >
				       </v-text-field>
				     <v-text-field v-model = "password" 
						   name = "password"
						   label = "Password"
						   type = "password"
				     >
					
				    </v-text-field>
			   	</v-card-text>	
                             <v-divider light></v-divider>
			     <v-card-actions>
				<v-btn rounded to='/signup' dark color="indigo">SignUp</v-btn>
				<v-spacer> </v-spacer>
				<v-btn rounded dark color="primary" @click.prevent="login()">
					LogIn
					<v-icon></v-icon>
				</v-btn>
			     </v-card-actions>
			   </v-card>
			</v-form>
		</v-flex>
	</v-layout>
   </v-container>
 </v-app>
</template>

<script>

import { logHook } from '../hooks/loginHook.js'; 
 
export default{
	name: 'logIn',
	data:() => ({
		username: '',
		password: '',
		error: false,
		posts: [],
		title: ''
		}),
	methods:{
		login(){
		      localStorage.setItem('userName', this.username)
		      const path = `/posts/${this.username}`
 		      if(this.$route.path != path)
		      	this.$router.push(path);
			   //{ name:'posts', params: { username: this.username } }
		}

		
 	}, hooks(){
		
		return logHook("Wellcome to the APP...");		
	}

};

</script>
