import {useData, useState, useMounted, useEffect, useComputed, useUpdated} from 'vue-hooks';
import io from 'socket.io-client';
let socket;
import onMessageSound from '../parts/ios_whatsapp_message.mp3';
import axios from 'axios';
import FormData from 'form-data'

export function loginHook(value){


//const urlAddress = 'http://localhost:5000/api/files/';
//const ENDPOINT = 'http://localhost:5000';
//const url = 'http://localhost:5000/api/files/uploads/';

const urlAddress = 'api/files/';
const url = 'api/files/uploads/';
const ENDPOINT = 'https://fast-citadel-36819.herokuapp.com/';

const data = useData({isShowing:value});
const [message, setMessage] = useState('');
const [messages, setMessages] = useState([]);
const [activeUsers, setActiveUsers] = useState([]);
const [typing, setTyping] = useState('');
const [file, setFile] = useState('');
const [uploadPercentage, setUploadPercentage] = useState(0);

const room = 'app';
var browserBackRefreshCloseMessage = "browser action";
var keyBoardRefreshMessage = "refresh button is pressed keyboard";
var isReconnect = false;
 

useEffect(()=> {
    
    socket = io(ENDPOINT);
    document.title = 'You are: ' +  data.isShowing; 
    
    const name = data.isShowing;

     socket.emit('join', {name, room}, ()=>{
	 
	 	
	 });
	
  },[ENDPOINT]);

useEffect(()=> {
  		history.pushState(null, document.title, location.href);
		window.addEventListener('popstate', function (event){
  			history.pushState(null, document.title, location.href);
 					});
		 
		//this.$once("hook:beforeDestroy", () => {
      		//	window.removeEventListener("beforeunload", this.preventNav);
    		//})	
 

  },[ENDPOINT]);
//return () => {
//		socket.emit('disconnect');
//		socket.off();
//	}

useEffect(()=> {

	socket.on('message', (message)=>{
		 
		if(message.user===data.isShowing)
	        	 document.title = 'You: ' + message.text;
		else{
			 window.navigator.vibrate(200);
			 document.title = message.user + ': ' + message.text;
			 beep();
		}	
	})
	

}, [ENDPOINT], [message]);

useEffect(()=> {
	socket.on('message', (message)=>{
	        setMessages([...messages, message]);
		  	})
	
},[messages]);

useEffect(()=> {

	socket.on('users', (users)=>{
		  
		 var activeUse = [];
		 activeUse=Object.values(users)[0].split(',');
		 setActiveUsers(activeUse);
				 
		})
	 
	 },[ENDPOINT], [activeUsers]);

useEffect(()=> {

	socket.on('isTyping', (typing)=>{
		  console.log(typing);
		  setTyping(typing);
		  setTimeout(function() {  setTyping(''); }, 2000);		 
		})
	 
	 },[ENDPOINT], [typing]);

const sendMessage = (event) => {
	event.preventDefault();
 
	if(message){
	   socket.emit('sendMessage', message, () => {setMessage('')});
	}
}

const isTyping = (event) =>{
	event.preventDefault();

	console.log("user is typing...");
	socket.emit('isTyping', 'user is typing...', () => {setTyping('')});

}

const logOff = () => {
	 
	console.log("disconnect");
	socket.emit('disconnectLog', 'disconnect user');
	socket.off();	 
}

const beep = () => {
	 const sound = new Audio(onMessageSound);
	 sound.load();
	 sound.play();
	}


const saveFile = (event) => {
	 
	if(file){
	   // Create File
	   let dataForm = new FormData();
	   dataForm.append('file', file);
	   const res = axios.post(urlAddress, dataForm, { 
	        headers: {
    			'accept': 'application/json',
    			'Accept-Language': 'en-US,en;q=0.8',
    			'Content-Type': 'multipart/form-data',
  			},
			onUploadProgress: progressEvent => {
				setUploadPercentage(parseInt(Math.round((progressEvent.loaded*100)/progressEvent.total)))
				setTimeout(function() {  setUploadPercentage(0); }, 10000);
				}
			});		
		}
	setFile('');
}

const getFile = (myFile) => {

	return new Promise(async (resolve,reject) => {
		const res = await axios.put(url,{myFile});
		const dat = res.data;
		resolve(dat);
		})
}

console.log(message,messages);
return{
	data,
	message,
	messages,
	activeUsers,
	typing,
	file,
	uploadPercentage,
	setMessage,
	sendMessage,
	setFile,
	saveFile,
	logOff,
	isTyping,
	getFile
	}
}
